import React, { Component } from 'react';
import moment from 'moment';

import dateUtil from '../utils/dateUtil'

const m = new Map(); // Map
m.set('1', '');
m.set('2', '');
m.set('3', '');
m.set('4', '');
m.set('5', '');
m.set('6', '');
m.set('0', '');

class WeekDisplay extends Component {
    constructor(props) {
        super(props);
      }
  render() {
    const date = this.props.date;
    let dayText;
    if(date){
        let dateMom = moment(date);
        let todayMom = moment();

        dayText = dayText = dateUtil.getTimeSpan(dateMom, todayMom, this.props.is)
        //console.log(date, dayText)
    }
    //console.log('date', date)
    return (
        <React.Fragment>
            {date?
            <div style={{float:'left', display:'inline'}}>({dateUtil.getWeekDameName(date)})</div>
            :
            <div/>
            }
        </React.Fragment>
    );
  }
}

export default WeekDisplay;
